.elfinder .elfinder-quicklook.elfinder-quicklook-fullscreen {
  z-index: 1000 !important;
}
.elfinder .elfinder-quicklook .elfinder-quicklook-title {
  line-height: 18px;
}
.elfinder .elfinder-cwd-wrapper {
  padding: 0 !important;
}
.elfinder .elfinder-navbar {
  max-width: 33.333%;
}
.elfinder .elfinder-cwd.elfinder-cwd-view-icons .elfinder-cwd-file {
  margin: 0 !important;
}
.elfinder .elfinder-cwd.elfinder-table-header-sticky table {
  padding: 0;
  border: 0;
}
.elfinder .elfinder-cwd .elfinder-cwd-file {
  border-radius: 0 !important;
}
.elfinder .elfinder-cwd .elfinder-cwd-file > * {
  color: #212121 !important;
}
.elfinder .elfinder-cwd .elfinder-cwd-file.ui-state-hover {
  background: rgba(158, 203, 255, 0.5);
}
.elfinder .elfinder-cwd .elfinder-cwd-file.ui-selected {
  background: rgba(118, 182, 255, 0.5);
  outline-offset: -1px;
  outline: 1px solid #0071a1;
}
.elfinder .elfinder-cwd .elfinder-cwd-file .elfinder-cwd-file-wrapper {
  background: none !important;
}
.elfinder .elfinder-cwd .elfinder-cwd-file .elfinder-cwd-filename {
  background: none !important;
}
.elfinder .elfinder-cwd .elfinder-cwd-file .ui-state-hover, .elfinder .elfinder-cwd .elfinder-cwd-file .ui-selected {
  background: none !important;
}
.elfinder .elfinder-cwd > table {
  padding: 0 !important;
}
.elfinder .elfinder-cwd > table tbody td:first-child {
  position: static;
}
.elfinder .elfinder-statusbar {
  border-radius: 0 !important;
}
.elfinder .elfinder-quicklook, .elfinder .ui-dialog {
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
}